.container {
    text-align: center;
    margin-top: 20px;
}

button {
    margin: 5px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    border: 1px solid #ccc;
    background-color: #f0f0f0;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #ddd;
}

.response, .logs {
    margin-top: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fafafa;
}

h2 {
    color: #333;
}

h3 {
    color: #555;
}

ul {
    list-style: none;
    padding: 0;
}

li {
    margin: 5px 0;
}
